//------------------General---------------------
html {
  font-size: 62.5%;
  scroll-behavior: smooth;
}


* {
  box-sizing: border-box;
  font-size: map-get($font-size, normal);
  list-style-type: none;
  outline: none;
  border: none;
  text-decoration: none;
  padding: 0;
  margin: 0;
}

body {
  font-family: map-get($font-primary, medium), $font-default;
  color: $color-primary;
  background: $bg-primary;
}
h2 {
  font-size: map-get($font-size, medium);
  text-align: center;
}

h3 {
  font-size: map-get($font-size, meidum);
  font-weight: normal;
  text-align: center;
}

a {
  text-decoration: none;
  color: $color-primary;
}

button {
  cursor: pointer;
  background: none;
}
