//-------------------Home----------------------
.mainHome {
  margin: 8rem auto 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 2rem;
  width: 60%;
  height: 100%;
  a {
    color: white;
  }
  &__img {
    position: relative;
    display: block;
    &::before {
      content: "";
      @include absolute;
      opacity: 0;
      transition: 0.3s ease-in-out;
      z-index: 5;
      background: rgba($bg-primary, $alpha: 0.4);
    }
    &:hover::before {
        opacity: 1;
      }
  }
  img {
    width: 40rem;
  }
}
