//-------------------Follow----------------------
.follow-container {
  position: fixed;
  z-index: 10;
  bottom: 1%;
  right: 7%;
  display: flex;
  gap: 2rem;
  a {
    max-width: 4rem;
    max-height: 4rem;
    button {
      max-width: 4rem;
      max-height: 4rem;
      svg {
        fill: $color-primary;
        transition: transform 0.3s ease-in-out;
        &:hover {
          fill: #cccccc;
          transform: scale(1.1);
          transition: transform 0.3s ease-in-out;
        }
        path {
          width: 12px;
          height: 12px;
        }
      }
    }
  }
}
