//-------------------VideoCine----------------------
//Main
.main {
  position: absolute;
  overflow: hidden;
  width: 100%;
  height: 100%;
  top: -3.4vmax;
  left: 0;
  &__wrapper {
    position: relative;
    padding-bottom: 56.25%;
    filter: blur(0.4rem);
    transition: 1s ease-in-out;
    iframe {
      display: block;
      background-color: $bg-primary;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      pointer-events: none;
    }
  }
}
