//------------------Variables ---------------------

//------------------General---------------------
//---------Background---------
$bg-primary: #000000;
$bg-secondary: transparent;
$bg-tertiary: #cccccc;
$bg-input: #fdfdfd;
$bg-submit: #cccccc;

$Border-color: #7f7e7e;
//---------Color---------
$color-primary: #fdfdfd;
$color-secondary: #cccccc;
$color-tertiary: #e6e6e6;
$color-contrast: #000000;
$color-accent: #fff;
$color-valid: #69A664;

//---------FontFamily---------
$font-primary: (
  black: "RalewayBlack",
  bold: "RalewayBold",
  regular: "RalewayRegular",
  medium: "RalewayMedium",
  light: "RalewayLight",
  thin: "RalewayThin",
);
$font-secondary: "CambayRegular";
$font-default: sans-serif;
//---------FontSize---------
$font-size: (
  big: clamp(4rem, 9.6vmin, 8rem),
  medium: clamp(1.5rem, 4vmin, 3rem),
  middle: clamp(1.2rem, 3vmin, 2.3rem),
  normal: clamp(0.8rem, 2vmin, 1.6rem),
  small: 0.5rem,
);
$font-size-icons: clamp(1rem, 2.8vmin, 2rem);

//------------------Menu Burger---------------------
$backgroundColorMenu: rgba(#000, 70%);
//------------------Header---------------------
//------------------Article ---------------------
$colorArticle: #fff;
$backgroundColorArticle: rgba(#000, 80%);

//------------------Aside ---------------------
$colorAside: #fff;
$backgroundColorAside: rgba(#000, 77%);
$colorArrow: #fff;

//------------------Footer---------------------
$backgroundColorFoot: rgba(#000, 90%);

//------------------Responsive---------------------

$breakpoints: (
  mobile: 480px,
  tablette: 768px,
  Landtop: 1024px,
  desktop: 1200px,
);
