//------------------Header ---------------------
.header {
  position: relative;
  display: flex;
  gap: 2rem;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  height: 7rem;
  padding: 0 clamp(2rem, 6.3%, 6rem);
  z-index: 6;
  background-color: $bg-secondary;
  color: $color-secondary;
  h1, h2 {
    margin-top: 1rem;
  }
  h1 {
    font-size: clamp(3rem, 4vmin, 7.2rem);
    font-weight: 500;
    color: $color-secondary;
    transition: 0.3s ease-in-out;
    &:hover {
      color: $color-primary;
    }
  }
  h2 {
    font-size: clamp(3rem, 4vmin, 7.2rem);
    font-weight: 500;
    color: $color-secondary;
    transition: 0.3s ease-in-out;
  }
}
