//-------------------CTA----------------------
.cta {
  position: fixed;
  z-index: 10;
  bottom: 1%;
  right: 1%;
  &__bubble {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 1.5rem;
    border-radius: 50%;
    background: rgba($color-secondary, $alpha: 0.4);
    cursor: pointer;
    button {
      max-width: 4rem;
      max-height: 4rem;
      svg {
        fill: $color-primary;
      }
    }
    &::before {
      content: "";
      @include absolute;
      opacity: 0;
      transition: 0.3s ease-in-out;
      z-index: -1;
      background: rgba($color-primary, $alpha: 0.4);
      border-radius: 50%;
    }
    &:hover::before {
      opacity: 1;
    }
    button {
      display: flex;
      align-items: center;
      justify-content: center;
      color: $color-primary;
    }
  }
  //-------------------CTA OPENED----------------------
  &__opened {
    position: fixed;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    width: 37rem;
    border-radius: 1.4rem;
    border: solid 0.1rem $color-primary;
    z-index: 10;
    bottom: 10%;
    padding: 2rem;
    right: 1%;
    background: rgba($bg-primary, $alpha: 1);
    &__welcome {
      display: flex;
      flex-direction: column;
      align-items: center;
      width: 33rem;
      &__title {
        text-align: center;
        font-size: map-get($font-size, medium);
      }
      &__p {
        margin-top: 3rem;
        font-size: map-get($font-size, normal);
        font-weight: 900;
      }
      ul {
        padding: 2rem 0 2rem 0;
        li {
          margin-left: 1.5rem;
          padding-left: 1rem;
          list-style-type: circle;
          font-weight: 900;
          &:last-child {
            margin-top: 0.6rem;
          }
        }
      }
      .separating {
        width: 100%;
        height: 0.1rem;
        background-color: $color-secondary;
      }
      h3 {
        margin-top: 2rem;
        font-size: map-get($font-size, middle);
        font-weight: 900;
      }
      img {
        margin: 2rem 0;
        max-width: 50%;
      }
      form {
        display: flex;
        flex-direction: column;
        gap: 2rem;
        width: 30rem;
        padding: 2rem 0 2rem 0;
        font-family: $font-secondary, $font-default;
        &:valid {
          & > .cta__opened__welcome__submit > button {
            background: $color-primary;
            font-weight: 900;
          }
        }
        & > div > * {
          border-radius: 1rem;
          &:focus {
            background: $color-primary;
          }
          &:valid:not(button) {
            background: $color-primary;
          }
        }
        label {
          align-self: flex-start;
          font-size: map-get($font-size, normal);
        }
        input {
          width: 30rem;
          height: 4.1rem;
          padding: 1rem 1.6rem 1rem 4.5rem;
          background-color: $color-primary;
        }

        textarea {
          width: 30rem;
          height: 16rem;
          padding: 1rem;
          resize: none;
          background-color: $color-primary;
        }
      }
      //-------------------REQUEST-SELECT----------------------
      &__with-selection {
        position: relative;
        display: flex;
        flex-direction: column;
        border-radius: 1rem;
        cursor: pointer;
        select {
          width: 30rem;
          height: 4.1rem;
          padding: 1rem 1.6rem 1rem 4.5rem;
          background: $bg-input;
          color: $color-contrast;
          cursor: pointer;
          appearance: none;
          &:hover {
            background-color: $bg-tertiary;
          }
        }
        span {
          position: absolute;
          display: block;
          top: 50%;
          transform: translateY(-50%);
          right: 5%;
          width: 2rem;
          height: 2rem;
          background-image: url("../assets/img/contact/chevron-bas.svg");
          background-position: 100% center;
          background-size: 100%;
          background-repeat: no-repeat;
        }
        i {
          position: absolute;
          left: 0;
          top: 0;
          color: $bg-primary;
          font-size: $font-size-icons;
          padding: 0.9rem 1.6rem;
        }
      }
      &__field {
        position: relative;
        display: flex;
        flex-direction: column;
        gap: 0.5rem;
        & > i {
          position: absolute;
          left: 0;
          top: 3.5rem;
          color: $bg-primary;
          font-size: $font-size-icons;
          padding: 0.9rem 1.6rem;
        }
      }
      &__submit {
        display: flex;
        justify-content: center;
        button {
          padding: 1.5rem;
          width: 11rem;
          height: 4.9rem;
          line-height: 100%;
          background-color: $bg-submit;
          transition: transform 0.3s ease-in-out;
          &:hover {
            background-color: $color-primary;
            transform: scale(1.1);
            transition: transform 0.3s ease-in-out;
          }
        }
      }
      //-----------Chat tawkTo-----------
      &__chatTawkTo {
        min-height: 44rem;
        padding: 2rem 0;
        & div {
          position: relative !important;
        }
      }
    }
    &__list {
      display: flex;
      align-items: center;
      justify-content: center;
      gap: 2rem;
      margin-top: 2rem;
      button {
        svg {
          fill: $color-primary;
          transition: 0.3s ease-in-out;
          &:hover {
            fill: #cccccc;
            transform: scale(1.1);
            transition: 0.3s ease-in-out;
          }
        }
      }
    }
    &__tawkto {
      color: white;
    }
  }
}
.tawk-overlay {
  position: relative !important;
}
.disabled {
  display: none;
}
.widget-container {
  flex-direction: column;
}
